import React, { createContext, useContext, useEffect, useState } from 'react';

const REMOTE_CONTENT_URL = 'https://logovobezdelnikov.com/data.json';
const LOCAL_FALLBACK_URL = '/data.template.json';

const ContentContext = createContext({
  content: {},
  loading: true,
  error: null,
});

const mergeDeep = (fallback, remote) => {
  if (!remote || typeof remote !== 'object' || Array.isArray(remote)) {
    return remote ?? fallback;
  }
  const result = { ...fallback };
  Object.keys(remote).forEach((key) => {
    const remoteValue = remote[key];
    const fallbackValue = fallback ? fallback[key] : undefined;
    if (remoteValue && typeof remoteValue === 'object' && !Array.isArray(remoteValue)) {
      result[key] = mergeDeep(fallbackValue || {}, remoteValue);
    } else {
      result[key] = remoteValue;
    }
  });
  return result;
};

const mergeContent = (fallback = {}, remote = {}) => {
  if (!remote || typeof remote !== 'object') return fallback;
  return {
    ...fallback,
    ...remote,
    pwTexts: mergeDeep(fallback.pwTexts || {}, remote.pwTexts || {}),
    games: remote.games && typeof remote.games === 'object'
      ? { ...fallback.games, ...remote.games }
      : fallback.games,
    programs: Array.isArray(remote.programs) && remote.programs.length > 0
      ? remote.programs
      : fallback.programs,
    gameIndex: Array.isArray(remote.gameIndex) && remote.gameIndex.length > 0
      ? remote.gameIndex
      : fallback.gameIndex,
    main: mergeDeep(fallback.main || {}, remote.main || {}),
    contacts: mergeDeep(fallback.contacts || {}, remote.contacts || {}),
  };
};

export const ContentProvider = ({ children }) => {
  const [content, setContent] = useState({});
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    let cancelled = false;
    const controller = typeof AbortController !== 'undefined' ? new AbortController() : null;

    const fetchJson = async (url, options = {}) => {
      const response = await fetch(url, options);
      if (!response.ok) throw new Error(`HTTP ${response.status}`);
      return response.json();
    };

    const loadContent = async () => {
      let fallbackData = {};

      try {
        fallbackData = await fetchJson(LOCAL_FALLBACK_URL, { cache: 'no-store' });
        if (!cancelled) {
          setContent(fallbackData);
        }
      } catch (fallbackErr) {
        if (!cancelled) {
          // keep empty content but note fallback failure
          setError((prev) => prev ?? fallbackErr?.message ?? '    ');
        }
      }

      try {
        const remoteData = await fetchJson(REMOTE_CONTENT_URL, {
          signal: controller ? controller.signal : undefined,
          cache: 'no-store',
        });
        if (cancelled) return;
        setContent((prev) => mergeContent(fallbackData && Object.keys(fallbackData).length ? fallbackData : prev, remoteData));
        setError(null);
      } catch (remoteErr) {
        if (!cancelled) {
          setError((remoteErr && remoteErr.message) || '    ');
        }
      } finally {
        if (!cancelled) setLoading(false);
      }
    };

    loadContent();
    return () => {
      cancelled = true;
      if (controller) controller.abort();
    };
  }, []);

  return (
    <ContentContext.Provider value={{ content, loading, error }}>
      {children}
    </ContentContext.Provider>
  );
};

export const useContent = () => useContext(ContentContext);
